import { useParams } from 'react-router-dom';
import { Link } from 'react-router-dom';


const Article = () => {
  const { id } = useParams();
  return (
    <><div>
          <h2> {id}</h2>
      </div>
      <div>
              <Link to="/articles"> </Link>
      </div>
    </>
  );
};

export default Article;