import { useParams } from 'react-router-dom';
import { Link } from 'react-router-dom';

const data = {
  velopert: {
    name: '',
    description: '  ',
  },
  gildong: {
    name: '',
    description: '   ',
  },
};

const Profile = () => {
  const params = useParams();
  const profile = data[params.username];

  return (
    <><div>
      <h1> </h1>
      {profile ? (
        <div>
          <h2>{profile.name}</h2>
          <p>{profile.description}</p>
        </div>
      ) : (
        <p>  .</p>
      )}
      <div>
        <Link to="/"> </Link>
      </div>
    </div></>
  );
};

export default Profile;